import { Component, OnInit } from '@angular/core';
import { ShareServiceService } from '../services/share-service.service';
import * as jspdf from 'jspdf';  
import html2canvas from 'html2canvas';
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';

declare var $: any;
declare var window: any;


@Component({
  selector: 'app-printpage',
  templateUrl: './printpage.component.html',
  styleUrls: ['./printpage.component.css']
})
export class PrintpageComponent implements OnInit {
  printData: any;
  spinnerConfig: ISpinnerConfig;
  showSpinner: boolean;
  constructor(
            public dataService:ShareServiceService
  ) { }

  ngOnInit() {
    this.getPrintData()
    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.spin_3,
      size: "4rem",
      color: "#1574b3"
  };
  }
  getPrintData(): any {
    this.printData=this.dataService.getArData()
    console.log(this.printData);
    
  }
  printPDF() {
   
  var divToPrint=document.getElementById('toPrint');

  var newWin=window.open('','Print-Window');

  newWin.document.open();

  newWin.document.write('<html><body onload="window.print()">'+divToPrint.innerHTML+'</body></html>');

  newWin.document.close();

  setTimeout(function(){newWin.close();},10);
  }
  public captureScreen()  
  {  
    this.showSpinner = true;
    var data = document.getElementById('toPrint');  
    html2canvas(data).then(canvas => {  
      // Few necessary setting options  
      var imgWidth = 208;   
      var pageHeight = 305;    
      var imgHeight = canvas.height * imgWidth / canvas.width;  
      var heightLeft = imgHeight;  
  
      const contentDataURL = canvas.toDataURL('image/png')  
      let pdf = new jspdf('p', 'mm', 'a4'); // A4 size page of PDF  
      var position = 0;  
      pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight)  
      pdf.save(`${this.printData.arNumber}.pdf`); // Generated PDF  
      this.showSpinner=false 
    });  
  }  

}
