import { Component, OnInit } from '@angular/core';
import * as $ from "jquery";
import {Router} from "@angular/router";
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';
import swal from 'sweetalert'

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.css']
})
export class LandingComponent implements OnInit {

  showSpinner: boolean;
  spinnerConfig: ISpinnerConfig;
  $:any
  currentUserDash: string;
  constructor(
    public route:Router
  ) { }

  ngOnInit() {
    this.getcurrentUserDash();
    this.js()
    "use strict"; // Start of use strict
    // Toggle the side navigation
     this.js();
    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.spin_3,
      size: "4rem",
      color: "#1574b3"
  };

  }
  getcurrentUserDash(): any {
    this.currentUserDash=JSON.parse(localStorage.getItem('currentUserDash')).userDetails;
    console.log(this.currentUserDash)
  }

  logout(){
    this.showSpinner=true
    localStorage.removeItem('currentUserDash');
    swal({
      title:'Logged out',
      icon:'success'
    })
    setTimeout(()=>{
      swal.close()
    },2000)
    this.route.navigate(['/login'])
    this.showSpinner=false
  }
js(){
  

      $("body").toggleClass("sidebar-toggled");
      $(".sidebar").toggleClass("toggled");

  
}
}


