import { Component, OnInit,ViewChild, ɵConsole } from '@angular/core';
import {environment} from '../../environments/environment';
import { HttpClient,HttpHeaders ,} from '@angular/common/http';
import swal from 'sweetalert';
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';
import { Router } from '@angular/router';
import { ShareServiceService } from '../services/share-service.service';
import { NetworkProvider } from '../services/network';
import { element } from '@angular/core/src/render3';


@Component({
  selector: 'app-ar-dashboard',
  templateUrl: './ar-dashboard.component.html',
  styleUrls: ['./ar-dashboard.component.css']
})
export class ARDashboardComponent implements OnInit {

  showSpinner: boolean;
  spinnerConfig: ISpinnerConfig;
  public baseURL=environment.baseUrl;
  public ArList: any = [];
  public hospitals:any=[]
  public arLists:any=[]
  public detailsToFind={
    admissionDate:'',
    district:'',
    hospitalDirectorate:'',
    hospitalName:''
  }
  currentArDetails: any;

  constructor(
    public shareServive:ShareServiceService,
    public dataServive:NetworkProvider,
    public http:HttpClient,
    public router:Router
  ) {}
  ngOnInit() {
    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.spin_3,
      size: "4rem",
      color: "#1574b3"
  };
  }
  clearForm(){
    this.detailsToFind.hospitalDirectorate=''
    this.detailsToFind.district=''
    this.detailsToFind.hospitalName=''
    this.hospitals=[]
    this.detailsToFind.admissionDate=''
  }

  findHospitals(){
    this.showSpinner = true;
    let data={
      district:this.detailsToFind.district,
      hospitalDirectorate:this.detailsToFind.hospitalDirectorate
    }
    this.dataServive.postData(this.baseURL+"/api/v1/hospitals/hospitalLists",data)
    .subscribe(res=>{
      this.hospitals=[];
      this.hospitals=res;
      this.hospitals = this.hospitals.data;
      this.showSpinner = false;
      setTimeout(()=>{
        this.showSpinner = false
      },4000)
    })
  }

  findListsAR(){
    this.showSpinner = true;
    let admissionDate=JSON.stringify(this.detailsToFind.admissionDate)
    let admissionD=admissionDate.slice(1,11)
    let data={
      currentUserID:this.detailsToFind.hospitalName,
      admissionDate:admissionD
    }
    this.dataServive.postData(this.baseURL+"/api/v1/accidentRegisters/arLists",data)
    .subscribe(res=>{
      this.ArList=[];
      this.arLists=res;    
     if(this.arLists && this.arLists.code==200){
      swal({
        title:this.arLists.status,
        icon:'success'
      })
      setTimeout(()=>{
        swal.close();
      },2000)
      let arLists=this.arLists.data
      arLists.forEach(list => {
        let data = {
          _id:list._id,
          ar_no:list.arNumber,
          name:list.patientInformation.name,
          admission_date:list.patientInformation.arrivalDate.slice(0,10),
          completed:list.completed
        }

        this.ArList.push(data);
      });
     }else{
      swal({
        title:this.arLists.status,
        icon:'error'
      })
      setTimeout(()=>{
        swal.close();
      },2000)
     }
     this.showSpinner= false
      this.clearForm();
    })
  }

  toPrintPage(v:String){
    let _id=v
    console.log(_id);
    this.arLists['data'].forEach(element => {
      if(element._id==_id){
        this.shareServive.setArData(element)
        this.currentArDetails=element
      }
    });

  this.showSpinner=true

  this.dataServive.postData(this.baseURL+"/api/v1/arDownloads/download",this.currentArDetails)
  .subscribe(res=>{
   
     this.showSpinner=false 

    const bearer ='data:application/pdf;base64,'
    const pdfInBase64 = bearer+res['data'];
     const linkSource = pdfInBase64;
        const downloadLink = document.createElement("a");
        const fileName = `${this.currentArDetails.arNumber}.pdf`;

        downloadLink.href = linkSource;
        downloadLink.download = fileName;
        downloadLink.click();
  }) 
  }
  districts=[
    'Test',
    'Chennai',
    'Cuddalore',
    'Kancheepuram',
    'Thiruvallur',
    'Thiruvannamalai',
    'Vellore',
    'Villuppuram',
    'Kallakurichi',
    'Dharmapuri',
    'Krishnagiri',
    'Namakkal',
    'Salem',
    'Erode',
    'Thiruppur',
    'Coimbatore',
    'Nilgiris',
    'Madurai',
    'Theni',
    'Dindigul',
    'Sivagangai',
    'Virudhunagar',
    'Ramanathapuram',
    'Thirunelveli',
    'Thoothukudi',
    'Kanyakumari',
    'Pudhukkottai',
    'Ariyalur',
    'Nagapattinam',
    'Perambalur',
    'Thanjavur',
    'Thiruchirappalli',
    'Karur',
    'Thiruvarur'

]
}
