import { Component, OnInit } from '@angular/core';
import {environment} from '../../environments/environment';
import { HttpClient,HttpHeaders ,} from '@angular/common/http';
import { config } from 'rxjs';
import swal from 'sweetalert';
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';
import { NetworkProvider } from '../services/network';



@Component({
  selector: 'app-hospital-status',
  templateUrl: './hospital-status.component.html',
  styleUrls: ['./hospital-status.component.css']
})
export class HospitalStatusComponent implements OnInit {

  showSpinner: boolean;
  spinnerConfig: ISpinnerConfig;
  public baseUrl=environment.baseUrl
  public hospitals:any;
  public statusHospital
  public activeHospitals:any=[];
  public deActiveHospitals:any=[];
  public hospitalStatus:any={
    isActive:''
  }
  constructor(
    public http:HttpClient,
    public dataService:NetworkProvider
  ) { }

  ngOnInit() {
   this.getAllHospitals();
   this.spinnerConfig = {
    placement: SPINNER_PLACEMENT.block_window,
    animation: SPINNER_ANIMATIONS.spin_3,
    size: "4rem",
    color: "#1574b3"
};
  }
  getAllHospitals(){
    this.showSpinner=true

    this.dataService.getData(this.baseUrl+"/api/v1/hospitals/allHospitals")
    .subscribe(res=>{
      this.hospitals=res;
      this.hospitals=this.hospitals.result;
      this.activeHospitals=[];
      this.deActiveHospitals=[]
      this.hospitals.forEach(hospital => {
        if(hospital.isActive===true){
          this.activeHospitals.push(hospital)
        }
        else if(hospital.isActive===false){
          this.deActiveHospitals.push(hospital)
        }
      });
    })
    this.showSpinner=false
  }


  statusAction(event,id){
    this.showSpinner =  true
    let data={
      isActive:event.target.checked,
      _id:id
    }
    this.dataService.postData(this.baseUrl+"/api/v1/hospitals/activateDeactivate",data)
    .subscribe(res=>{
      this.statusHospital=res
      if(this.statusHospital && this.statusHospital.code==200){
        swal({
          title:this.statusHospital.status,
          icon:'success'
        })
        setTimeout(()=>{
          swal.close()
        },2000)
      }else{
        swal({
          title:'Something went wrong',
          icon:'error'
        })
        setTimeout(()=>{
          swal.close()
        },2000)
      }
      this.getAllHospitals();
      this.showSpinner=false
    })
  }

}
