import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {LoginComponent} from '../app/login/login.component';
import {LandingComponent} from'../app/landing/landing.component';
import {ARDashboardComponent} from '../app/ar-dashboard/ar-dashboard.component';
import {AddHospitalComponent} from '../app/add-hospital/add-hospital.component';
import {HospitalStatusComponent} from '../app/hospital-status/hospital-status.component';
import { PrintpageComponent } from './printpage/printpage.component';


const routes: Routes = [
{path:'login',component:LoginComponent},
{path:'',pathMatch:'full',redirectTo:'login'},
{path:'printPage',component:PrintpageComponent},
{path:'landing',component:LandingComponent,
children:[
  {path:'dashboard',component:ARDashboardComponent},
  {path:'',pathMatch:'full',redirectTo:'dashboard'},
  {path:'addHospital',component:AddHospitalComponent},
  {path:'hospitalStatus',component:HospitalStatusComponent},
]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
