import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {environment} from '../../environments/environment';
import { HttpClient} from '@angular/common/http';
import swal from 'sweetalert';
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';
import { NetworkProvider } from '../services/network';




@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  showSpinner: boolean;
  spinnerConfig: ISpinnerConfig;
  errMsg={
    message:''
  }
 public baseUrl=environment.baseUrl;
 public userCridentials;
  constructor(
    public http:HttpClient,
    public route:Router,
    public dataService:NetworkProvider) { }
  ngOnInit() {
    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.spin_3,
      size: "4rem",
      color: "#1574b3"
  };
  }

  goToGateway(v) {
    this.showSpinner=true
    this.dataService.postData(this.baseUrl + "/api/v1/adminUsers/login", v).subscribe(res => {
      console.log(res);
      this.userCridentials = res;
      if (this.userCridentials.code === 200) {
        swal({
          title:this.userCridentials.status,
          icon:'success'
        })
        setTimeout(()=>{
          swal.close();
        },2000)
        localStorage.setItem('currentUserDash', JSON.stringify({ token: this.userCridentials.token, userDetails: this.userCridentials.data }));
        this.route.navigate(["/landing"])
      }
      else {
        swal({
          title:this.userCridentials.status,
          icon:'error'
        })
        this.errMsg.message = this.userCridentials.status
        this.route.navigate(['/login'])
      }
      this.showSpinner=false
    })
  }
}
